.size-slider {
    width: 100%;
    height: 650px;
    background-color: rgb(243, 189, 41);

}

.slides .slick-prev,
.slides .slick-next {
  position: absolute;
  top: 50%;
  z-index: 1;
}

.slides {
  position: relative;
  margin-bottom: 100px;
}

.slides .slick-prev,
.slides .slick-next {
  position: absolute;
  top: 50%;
  z-index:1;
}

.slides .slick-prev {
  left: 5%;
}

.slides .slick-next {
  right: 5%;
}

.otziv{
    font-size: 30px;
    font-weight: 900;
    text-align: start;
    margin-top: 100px;
    margin-left: 70px;
}

.name-otziv{
    font-size: 35px;
    font-weight: 900;
    text-align: left;
    padding: 0px 0 0 500px;
}

.text-otziv{
    font-size: 23px;
    text-align: left;
    padding: 60px 0 0 500px;
}

.img-otziv {
    position: absolute;
    width: 356px;
    height: 390px;
    // left: 85px;
    // right: 13100px;
    top: 171px;
    margin-left: 70px;

    background-image: url('../../assets/slider/1.png');
    opacity: 1;
}

.slick-dots button {
  font-size:0!important;
  padding:0;
}

.slick-dots li button:before {
  color: #ffffff !important;
  font-size: 15px !important;
}

.slick-dots li.slick-active button:before {
  opacity: 1 !important;
  color: #EF8F00 !important;
}

.slick-dots {
  margin-bottom: 68px !important;
}
.slick-dots li button:hover:before, .slick-dots li button:focus:before {
  opacity: 1 !important;
  color: #EF8F00 !important;
}
