#main-image-zaklad {
	background-image: url('./images/bg-3.png');
	background-size: 100% 100%;
	background-repeat: no-repeat;

	display: flex;
	justify-content: space-between;
	min-width: 1163px;
	max-width: 100%;
	min-height: 465px;
}

#main-image-zaklad #text {
	display: flex;
	flex-direction: column;
	padding: 50px 100px;
	text-align: justify;
}

#main-image-zaklad #text #big {
	font-size: 40px;
	font-family: Arial;
	font-style: normal;
	font-weight: 800;
	z-index: 1;
}

#main-image-zaklad #text #small {
	font-family: Arial;
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	z-index: 1;
}

#main-image-zaklad #rect {
	padding: 50px 100px;
	z-index: 1;
}

.rec-img{
	width: 500px;
	height: auto;
	margin: 150px 140px 0 0 ;

	
}